/* The navbar css */
nav {
    position: absolute;
    background-color: #1B242F;
    height: 50px;
    width: 100%;
    border-bottom: 2.5px #04C2C9 solid;
}

nav ul {
    float: left;
    margin-left: 110px;
}

nav ul li {
    display: inline-block;
    line-height: 18px;
    margin: 0 35px;
}

nav ul li a {
    color: white;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    transition: .5s;
}

nav ul li a:hover {
    color: #D62139;
}


.checkbtn {
    font-size: 30px;
    color: white;
    float: right;
    line-height: 50px;
    margin-right: 18px;
    cursor: pointer;
    display: none;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;

  }


#check {
    display: none;
}

@media (max-width: 952px) {
    nav ul li a {
        font-size: 16px;
    }
}
@media (max-width: 858px) {
    .checkbtn {
        display: block;
    }

    ul {
        position: absolute;
        width: 100%;
        height: 40pvh;
        background-color: #333333;
        top: 37px;
        left: -150%;
        text-align: left;
        transition: all .5s;
    }

    nav ul li {
        display: block;
        margin: 50px 30px;
        line-height: 30px;
    }

    nav ul li a {
        font-size: 20px;
    }
    a:hover,a.active {
        background: none;
        color: #000; 
    }
    #check:checked ~ ul {
        left: -126px;
    }

    .sticky {
        top: 0px;
      }
      
}

  

