/* Social animation */

.middle{
    position: relative;
    width: 100%;
    text-align: center;
    padding-top: 16px;
  }
  .btn{
    display: inline-block;
    width: 55px;
    height: 55px;
    background: #262F38;
    margin: 10px;
    color: #fff;
    overflow: hidden;
    position: relative;
  }
  .btn i{
    line-height: 60px;
    font-size: 22px;
    transition: 0.2s linear;
  }
  .btn:hover i{
    transform: scale(1);
    color: #f1f1f1; 
    animation: social .7s 1;
  }

  @keyframes social {
      0%{
        margin-top: 0px;
    }

      50%{
          margin-top: 100px;
      }

      100%{
          margin-top: 0px;
      }
  }
  .btn::before{
    content: "";
    position: absolute;
    width: 120%;
    height: 120%;
    background: #04c2c9;
    transform: rotate(0deg);
    left: -120%;
    top: 90%;
  }
  .btn:hover::before{
    animation: aaa .7s 1;
    top: -10%;
    left: -10%;
  }
  @keyframes aaa {
    0%{
      left: -110%;
      top: 90%;
      transform: rotate(0deg);
      border-radius: 100%;
      transform: scale(0.1);
    }50%{
      left: 10%;
      top: -30%;
      transform: rotate(45deg);
  
    }100%{
      top: -10%;
      left: -10%;
      transform: rotate(90deg);
  
    }
  }

  .up {
      height: 50px;
      width: 47px;
      background-color: #e31b6d;
      margin: auto;
      position: relative;
      top: -25px;
      line-height: 80px;
  }
